import React from 'react'
import style from './Dialogs.module.sass'
import Dialog from './Dialog/Dialog'
import { withRouter } from 'react-router-dom'

const Dialogs = props => {
    return (
        <div
            className={`${style.dialogs} ${
                props.match.params.id ? style.hide : ''
            }`}
        >
            <Dialog
                name='Антимаг'
                img='https://prodota.ru/uploads/news/imperavi/1594455672309.png'
                body='Я сменил(а) пол :D'
                id={2}
            />
            <Dialog
                name='Квопа'
                img='data:image/jpeg;base64,'
                body='Поиграем на миду? ;)'
                id={3}
            />
            <Dialog
                name='Огр-маг'
                img='https://gamepedia.cursecdn.com/dota2_gamepedia/e/e0/Ogre_Magi_icon.png'
                body='Я то уж поумнее тебя буду'
                id={4}
            />
            <Dialog
                name='Рубик'
                img='data:image/jpeg;base64,'
                body='Я не краду, я беру взаймы'
                id={5}
            />
        </div>
    )
}

export default withRouter(Dialogs)
